<template>
  <div id="app">

    <div id="parchment">
      <div id="contain" class="center">
      <router-view/>
      </div>
    </div>

    <svg>
      <filter id="wavy2">
        <feTurbulence x="0" y="0" baseFrequency="0.02" numOctaves="5" seed="1"></feTurbulence>
        <feDisplacementMap in="SourceGraphic" scale="2" />
      </filter>
    </svg>


  </div>
</template>

<script>
export default {
  name: 'app'
}
</script>


<style>
@import url('https://fonts.googleapis.com/css?family=Pirata+One|Bilbo+Swash+Caps&display=swap');

*, *:before, *:after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html, body {
  height: 100%;
}

body {
  background-image: url(https://images.unsplash.com/photo-1546484396-fb3fc6f95f98?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1350&q=80);
  background-size: cover;
}

main {
  postion: relative;
  width: 100%;
  height: auto;
}

#parchment {
  position: absolute;
  display: flex;
  width: 75%;
  /* center page with absolute position */
  top: 0%; left: 50%; transform: translate(-50%, 0);
  margin: 2em 0;
  padding: 4em;
  box-shadow: 2px 3px 20px black, 0 0 125px #8f5922 inset;
  /* v2.1 : borders effect with SVG : try to play with scale to change them */
  filter: url(#wavy2);
  /* v2.2 : Noise added for a vellum paper effect */
  background: #fffef0 url(data:image/png;base64,);
}

main p {
  font-size: 2.25em;
  color: #7F3300;
  margin-top: .75em;
  text-align: justify;
}

div p:not(:first-child)::first-letter {
  float: left;
  font: 1.7em/1em Morris;
  text-shadow: 1px 1px 1px #000;
  margin: 0 .5rem;
}

#labarum img, .cachet img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}
.center{
  margin: auto;
  width: 50%;
  padding: 10px;
}
</style>



